    justify-content: space-between;
}
.ApplicantHeaderBtn2{
    border-radius: 160px;
    background: rgb(43, 43, 43);
    color: rgb(255, 255, 255);
    font-family: PP Neue Montreal;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0.32px;
    text-align: center;
    width: 120px;
    height: 44px;
    padding: 8px;
    display: inline-flex;
    flex-direction: row;
    align-items: center;
    gap: 10px;
    justify-content: center;
    cursor: pointer;
    border: none;
}


.ApplicantHeaderBtn2:hover{
    box-sizing: border-box;
    border-bottom: 2px solid rgb(0, 0, 0);
    background: rgb(254, 194, 0);
    color: rgb(19, 19, 19);
}

.ApplicantHeaderBtn2:active{
    box-sizing: border-box;
    border: 1px solid rgb(254, 194, 0);
    border-radius: 160px;
    background: rgb(43, 43, 43);
    color: rgb(254, 194, 0);

}

