@media (min-width: 768px) {

    .ApplicantMainBody3{
        box-sizing: border-box;
        padding: 30px;
        border: 1px solid rgb(236, 236, 236);
        border-radius: 8px;
        width: 100%;
        background: rgb(255, 255, 255);
        display: inline-flex;
