.PaymentSkillsCtn {
    max-width: 310px;
    width: 100%;
}

.PaymentSkillsTop {
    color: rgb(10, 13, 19);
    font-family: PP Neue Montreal;
    font-size: 23px;
    font-weight: 500;
    line-height: 140%;
    width: 100%;
    letter-spacing: 0;
    text-align: center;
    display: inline-flex;
    flex-direction: row;
    align-items: center;
    gap: 5px;
    margin: 10px 0;
    justify-content: flex-start;
}

.paymentInputCtn{
    display: inline-flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
}

.paymentFormCtn{
    display: inline-flex;
    flex-direction: column;
}

.hr {
    width: 300px;
    height: 0.25px;
    background: rgb(172, 172, 172);
    border: none;
}

.checkboxCtn, .BtnCtn p{
    color: rgb(172, 172, 172);
    font-family: PP Neue Montreal;
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;
    letter-spacing: 0;
    text-align: left;
    display: inline-flex;
    flex-direction: row;
    gap: 10px;
    align-items: center;
    margin: 10px 0;


}

.BtnCtn{
    margin: 15px 0;
    display: inline-flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;
}

.BtnCtn p{
    text-align: center;
}

.LocalDepoCtn{
    display: inline-flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
}

.LocalDepo{
    display: inline-flex;
    flex-direction: column;
    align-items: flex-start;
}

.LocalDepo1{
    display: inline-flex;
    flex-direction: column;
    align-items: flex-start;
    gap:10px;
}

.LocalDepoInfoCtn{
    display: inline-flex;
    flex-direction: row;
    align-items: flex-start;
    gap: 5px;
    justify-content:center;
}

.LocalDepoInfo1{
    color: rgb(83, 83, 83);
    font-family: PP Neue Montreal;
    font-size: 14px;
    font-weight: 500;
    line-height: 140%;
    letter-spacing: 0;
    text-align: left;
}

.LocalDepoInfo2{
    color: rgb(83, 83, 83);
    font-family: PP Neue Montreal;
    font-size: 14px;
    font-weight: 400;
    line-height: 140%;
    letter-spacing: 0;
    text-align: left;
}

.LocalDepoInfo3 img{
    cursor: pointer;
   -webkit-user-drag: none;
}

.total1{

    color: rgb(83, 83, 83);
    font-family: PP Neue Montreal;
    font-size: 14px;
    font-weight: 500;
    line-height: 140%;
    letter-spacing: 0;
    text-align: left;
}

.total2{
    color: rgb(83, 83, 83);
    font-family: PP Neue Montreal;
    font-size: 12px;
    font-weight: 400;
    line-height: 140%;
    letter-spacing: 0;
    text-align: left;
}


.LocalDepo2{
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    gap: 15px;
    margin: 10px 0;
}

.LocalDepo3{
    align-items: flex-start;
    display: inline-flex;
    flex-direction: column;
    text-align: left;
    padding: 10px;
}

.LocalDepo3Ctn{
    margin-top: 10px;
    border-top: 1px dashed rgba(39, 39, 39, 0.5);

}


.LocalDepo2Text{
    color: rgb(0, 17, 34);
    font-family: PP Neue Montreal;
    font-size: 14px;
    font-weight: 400;
    line-height: 17px;
    letter-spacing: 0;
    text-align: center;
}

.LocalDepo2Text2{
    display: inline-flex;
    flex-direction: row;
    align-items: center;
    color: rgb(8, 56, 130);
    font-family: Poppins;
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
    letter-spacing: 0;
    gap: 10px;
    text-align: center;
    cursor: pointer;
}

.LocalDepo3Text2{
    color: rgb(83, 83, 83);
    font-family: Poppins;
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
    letter-spacing: 0;
    text-align: left;
}

.LocalDepo3Text1{
    color: rgb(83, 83, 83);
    font-family: Poppins;
    font-size: 14px;
    font-weight: 600;
    line-height: 21px;
    letter-spacing: 0;
    text-align: left;
}

@media (min-width: 425px) {
    .PaymentSkillsCtn {
        max-width: 400px;
        width: 100%;
    }

    .hr {
        width: 100%;
        height: 0.25px;
        background: rgb(172, 172, 172);
        border: none;
    }


    .LocalDepoInfo1{
        color: rgb(83, 83, 83);
        font-family: PP Neue Montreal;
        font-size: 16px;
        font-weight: 500;
        line-height: 140%;
        letter-spacing: 0;
        text-align: left;
    }

    .LocalDepoInfo2{
        color: rgb(83, 83, 83);
        font-family: PP Neue Montreal;
        font-size: 16px;
        font-weight: 400;
        line-height: 140%;
        letter-spacing: 0;
        text-align: left;
    }

}

@media (min-width: 760px) {
    .PaymentSkillsCtn {
        max-width: 575px;
        width: 100%;
        box-sizing: border-box;
        border: 1px solid rgb(217, 217, 217);
        padding: 30px;
        border-radius: 16px;
    }

    .total1{

        color: rgb(83, 83, 83);
        font-family: PP Neue Montreal;
        font-size: 16px;
        font-weight: 500;
        line-height: 140%;
        letter-spacing: 0;
        text-align: left;
    }

    .total2{
        color: rgb(83, 83, 83);
        font-family: PP Neue Montreal;
        font-size: 16px;
        font-weight: 400;
        line-height: 140%;
        letter-spacing: 0;
        text-align: left;
    }

    .paymentInputCtn{
        display: inline-flex;
        flex-direction: column;
        align-items: center;
    }

    .PaymentSkillsTop{
        gap: 20px;
    }

    .LocalDepoInfoCtn{
        display: inline-flex;
        flex-direction: row;
        align-items: center;
        gap: 20px;
        justify-content:center;
    }

}

@media (min-width: 1024px) {
    .PaymentSkillsCtn {
        max-width: 600px;
        width: 100%;
        box-sizing: border-box;
        border: 1px solid rgb(217, 217, 217);
        padding: 30px;
        border-radius: 16px;
    }

    .paymentInputCtn{
        display: inline-flex;
        flex-direction: column;
        align-items: center;
    }

}